<script>
// Replace with your actual WebSocket URL
const token = "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ0b2tlbl90eXBlIjoiYWNjZXNzIiwiZXhwIjoxNzM0NTMwNDI3LCJpYXQiOjE3MzMyMzQ0MjcsImp0aSI6ImJjMzQwNGUzY2QwZTQzYTk4YjA3YTFhMDA5Zjc4Nzc5IiwidXNlcl9pZCI6Nn0.I40F16jCFXABaJc4BadTJ1PKvFj81WYJqrFgtAPGnf8" // Get the token from local storage
const roomName = "test-room";  // Replace with dynamic room name if needed
const socket = new WebSocket(`ws://127.0.0.1:8000/ws/chat/${roomName}/?${token}`);

// Get DOM elements
const messageInput = document.getElementById('message-input');
const sendButton = document.getElementById('send-message');
const chatMessages = document.getElementById('chat-messages');

// Handle the WebSocket connection opening
socket.onopen = () => {

  console.log("Connected to WebSocket.");
};

// Handle receiving a message from the server
socket.onmessage = (event) => {
  const message = event.data;
  const messageElement = document.createElement("p");
  messageElement.textContent = message;
  chatMessages.appendChild(messageElement);
  chatMessages.scrollTop = chatMessages.scrollHeight;  // Scroll to the bottom
};

// Handle WebSocket errors
socket.onerror = (error) => {
  console.error("WebSocket error:", error);
};

// Handle the WebSocket connection closing
socket.onclose = () => {
  console.log("WebSocket connection closed.");
};

// Send a message when the send button is clicked
sendButton.addEventListener("click", () => {
  const message = messageInput.value.trim();
  if (message) {
    socket.send(JSON.stringify({ message: message, receiver: 1 }));
    messageInput.value = '';  // Clear the input
  }
});

// Optional: Allow sending the message with Enter key
messageInput.addEventListener("keypress", (event) => {
  if (event.key === "Enter") {
    sendButton.click();
  }
});
</script>
